nav {
  display: flex;
  justify-content: center;
  border-bottom: 1px solid black;
  padding: 5px;
  gap: 10px;
  background-color: gray;
  a {
    font-size: 19px;
    font-weight: bold;
    text-decoration: none;
    color: white;
    &:hover {
      color: rgb(7, 4, 4);
    }
  }
}
